export default class Home extends Vue {
    peer: RTCPeerConnection | null = null;

    mysocket: WebSocket | null = null

    channel: RTCDataChannel | null = null

    connectId = ''

    async connectUser() {
      this.start(true, null);
    }

    async start(isCaller: boolean, payload: {[key: string]: string} | null) {
      this.peer = new RTCPeerConnection(
        {
          iceServers: [
            // { urls: 'stun:23.21.150.121' },
            { urls: 'stun:stun.l.google.com:19302' },
            { urls: 'stun:stun1.l.google.com:19302' },
            { urls: 'stun:stun2.l.google.com:19302' },
            { urls: 'stun:stun3.l.google.com:19302' },
            // { urls: 'stun:stun4.l.google.com:19302' },
            // {
            //   urls: 'turn:numb.viagenie.ca',
            //   credential: 'turnserver',
            //   username: 'yanglung.huang@ecloudvalley.com',
            // },
            // WcF.4KvG,w*pErH)
          ],
        },
      );
      this.peer.addEventListener('icecandidate', (e) => {
        const datas = {
          action: 'candidate',
          payload: e.candidate,
        };
        this.mysocket!.send(JSON.stringify(datas));
      });
      this.peer.addEventListener('icecandidateerror', (e) => {
        console.log(e);
      });
      this.peer.addEventListener('connectionstatechange', (e) => {
        // console.log(e);
      });
      this.peer.addEventListener('datachannel', (e) => {
        console.log(e);
      });
      if (isCaller) {
        const offerOptions = {
          offerToReceiveAudio: true,
          offerToReceiveVideo: true,
        };
        const offer = await this.peer.createOffer(offerOptions);
        await this.peer.setLocalDescription(offer);
        const datas = {
          action: 'sdp',
          payload: offer,
        };
        if (payload && payload.sdp) {
          await this.peer.setRemoteDescription(new RTCSessionDescription(payload));
        }
        this.mysocket!.send(JSON.stringify(datas));
      } else {
        if (payload && payload.sdp) {
          await this.peer.setRemoteDescription(new RTCSessionDescription(payload));
        }
        const answer = await this.peer.createAnswer();
        await this.peer.setLocalDescription(answer);
        const datas = {
          action: 'sdp',
          payload: answer,
        };
        this.mysocket!.send(JSON.stringify(datas));
      }
      // console.log(this.peer);
    }

    async created() {
      this.mysocket = new WebSocket('wss://fkidh8bvce.execute-api.ap-southeast-1.amazonaws.com/dev');
      this.mysocket.onopen = () => {
        console.log('connection open');
      };
      this.mysocket.onmessage = async (res) => {
        const { payload } = JSON.parse(res.data);
        // console.log(payload);
        if (!this.peer) {
          await this.start(false, payload);
        } else if (payload && payload.sdp) {
          // console.log(123);
          // await this.peer!.setRemoteDescription(new RTCSessionDescription(payload));
        }
        if (payload && payload.sdp) {
          await this.peer!.setRemoteDescription(new RTCSessionDescription(payload));
        } else if (payload && payload.candidate) {
          // console.log(payload);
          await this.peer!.addIceCandidate(new RTCIceCandidate(payload));
        }
      };
    }

    async createChannel() {
      if (this.peer) {
        this.channel = await this.peer.createDataChannel(
          'demo',
          { negotiated: false },
        );
        this.channel.addEventListener('open', (e) => {
          console.log(e);
        });
        console.log(this.channel);
      }
    }

    async sendMessage() {
      if (this.channel) {
        this.channel.send('test');
      }
    }
}
